import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function pretaxProfitMarginTtm(tableName: string) {
  const title = "Pre-Tax Margin (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["pretaxProfitMarginTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Profit before taxes as a % of revenue.",
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about pre-tax margin?`,
              value: info.row.original.financialData.companyRatios?.data?.pretaxProfitMarginTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            (info.row.original.financialData.companyRatios?.data?.pretaxProfitMarginTtm ?? 0) * 100
          }
        />
      )
    },
  })
}

export { pretaxProfitMarginTtm }
